  components: {
    VideoList: () => import ('@/components/VideoList.vue'),
    TrackList: () => import ('@/components/TrackList.vue'),
  },
  data() {
    return {
      library: library,
      selectedVideo: null as Video | null,
    }
  },
  computed: {
  },
  methods: {
    onClick(video: Video) {
      console.log(video);
      this.selectedVideo = video;
    }
  }
